body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.h-pane {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-content: center;
          align-content: center;
}

.v-pane {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-content: center;
          align-content: center;
}

body {
  padding: 10px;
}

main>* {
  padding: 5px;
}

header>h1 {
  text-align: center;
}

.nav-link:hover{
  cursor: -webkit-grab;
  cursor: grab;
}

/* Removes blue outline from button links */
button:focus {outline:0;}

/* Preserve white spaces in span tags */
span{
  white-space: pre;
}
#search.v-pane {
    width: 50%;
}
#results ul{
    height: 80vh;
    overflow-y: scroll;
}

.highlight {
    background-color: yellow;
}

.result{
    cursor: -webkit-grab;
    cursor: grab;
}

.result>p{
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-right: 20px;
}

.result>.title{
    font-weight: bold;
    font-size: 1.3rem;
}

.result>.description{
    font-style: italic;
}

.result>.category{
    color: blue;
}
#control.v-pane {
    width: 50%;
}
